.banking-container {
  .header {
    font-size: 24px;
    font-weight: 600;
    margin-bottom: 10px;
  }
  .actions {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 30px;

    .title {
      font-size: 24px;
      font-weight: 600;
    }
    .buttons {
      display: flex;
      align-items: center;
      gap: 14px;
      .ant-btn {
        font-size: 12px;
        font-weight: 500;
      }
    }
  }
  .metrics {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
    .card {
      padding: 10px 14px;
      border-radius: 12px;
      min-width: 23%;
      .title {
        font-size: 14px;
        border-bottom: 1px dashed #20b75761;
        padding-bottom: 10px;
        margin-bottom: 10px;
        font-weight: 500;
      }
      .data {
        display: flex;
        align-items: center;
        justify-content: space-between;
        .value {
          .number {
            font-size: 18px;
            font-weight: 500;
          }
          .desc {
            margin-top: 4px;
            font-size: 14px;
            display: flex;
            font-weight: 300;
          }
          .negative {
            color: #ff6161;
          }
          .positive {
            color: #20b757;
          }
        }
        .graph {
          font-size: 40px;
          color: #20b757;
          height: 54px;
        }
      }
    }
    .ant-card-body {
      padding: 0;
    }
  }
  .account {
    margin-top: 40px;
    background: #fff;
    padding: 20px;
    border-radius: 10px;

    .title {
      font-size: 18px;
    }
    .accounts {
      .account-card {
        // display: flex;
        // flex-flow: row wrap;
        // gap: 20px;
        // justify-content: space-between;
        display: grid;
        // grid-template-columns: repeat(auto-fill, 31%);
        grid-gap: 30px;
        justify-content: space-between;
        grid-template-columns: 31% 31% 31%;
        .card {
          transition: box-shadow 0.3s ease, border 0.3s ease,
            background ease 0.5s;

          .ant-card-body {
            padding: 10px 14px;
            .top {
              display: flex;
              justify-content: space-between;
              margin-bottom: 10px;
              .header {
                display: flex;
                gap: 10px;
                .icon {
                  height: 40px;
                  width: 40px;
                  border-radius: 50%;
                  overflow: hidden;
                  border: 1px solid #ebecef;
                  img {
                    height: 100%;
                    width: 100%;
                  }
                }
                .text {
                  font-size: 14px;
                  .meta {
                    font-size: 11px;
                    opacity: 0.6;
                  }
                }

                margin-bottom: 10px;
              }
              .ant-dropdown-trigger {
                height: fit-content;
              }
            }
            .label-value {
              display: flex;
              gap: 4px;
              font-size: 12px;
              margin-bottom: 6px;
              .label {
                opacity: 0.8;
              }
              .value {
                font-weight: 500;
                min-width: 130px;
              }
            }
            .type {
              position: absolute;
              right: 0;
              bottom: 5px;
              background: #4371e9;
              padding: 0 10px;
              border-top-left-radius: 13px;
              border-bottom-left-radius: 13px;
              color: #fff;
              font-size: 12px;
              text-transform: capitalize;
            }
            .type.fixed {
              background: #4371e9;
            }
            .type.current {
              background: #ff6161;
            }
            .type.savings {
              background: #ffc861;
            }
          }
        }
        .card:hover {
          cursor: pointer;
          box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
          border: 1px solid #20b757;
          background: #20b75715;
        }
      }
      .account-card::after {
        content: "";
        flex: auto;
      }
    }
  }
}

::ng-deep {
  .buttons {
    .ant-btn {
      font-size: 14px;
    }
  }
  .ant-radio-button-wrapper:last-child {
    border-start-end-radius: 30px;
    border-end-end-radius: 30px;
  }
}

.dropdown-option {
  font-size: 12px;
  font-weight: 400;
}
